.custom-text-area {
  display: flex;
  flex-flow: wrap;
  min-height: 100vh;
  padding: 10px;
  flex-direction: column; }
  .custom-text-area .pseudo-textarea {
    width: 100%;
    height: 100%;
    flex: 2;
    min-height: 1rem;
    cursor: text; }
    .custom-text-area .pseudo-textarea .norm-text {
      min-height: 1rem; }
    .custom-text-area .pseudo-textarea .heading-1 {
      font-size: 2em;
      font-weight: bolder; }
    .custom-text-area .pseudo-textarea .heading-2 {
      font-size: 1.5em;
      font-weight: bolder; }
    .custom-text-area .pseudo-textarea .heading-3 {
      font-size: 1.17em;
      font-weight: bolder; }
  .custom-text-area textarea {
    margin: 0;
    padding: 0;
    border: 0;
    resize: none;
    overflow: hidden; }

.cursor {
  min-height: inherit;
  width: 1px;
  background-color: black;
  animation-name: blink;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  display: inline-block; }

@-webkit-keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-moz-keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-o-keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
